<template>
    <div id="submitpage">
        <loadings ref="load"></loadings>
        <div id="editor-container" class="immersive-container">
            <top-side
                    v-bind:class="{'problem-only': problem_open && !edit_open, 'editor-only': !problem_open && edit_open}">
                <slide-curtain style="display: none; height: 658.4px;">
                </slide-curtain>
                <left-side style="width: 49.82vw;">
                    <div v-bind:class="{'left-side-pre': true, 'container-pro': problem_open && !edit_open}">
                        <div class="prob-header animated pre-animated cm-performance-optimistic fadeInLeft">
                            <button class="btn btn-outline-secondary" id="backBtn"
                                    v-bind:onclick="'window.location.href = \'/problem.html?pid=' + encodeURIComponent(page_setting.pid)+'\''">
                                <i class="fa fa-hand-o-left" aria-hidden="true"></i>
                                Back
                            </button>
                            <info-badge data-toggle="tooltip" data-placement="top" title=""
                                        data-original-title="AC Rate">
                                <i class="fa fa-star" aria-hidden="true"></i>
                                {{(problem.submitStatistics.allSubmitTimes == 0)? '-&#45;&#45;':
                                (100*problem.submitStatistics.acSubmitTimes/problem.submitStatistics.allSubmitTimes).toFixed(2)
                                + "%"}}
                            </info-badge>
                            <info-badge data-toggle="tooltip" data-placement="top" title=""
                                        data-original-title="Time Limit">
                                <i class="fa fa-clock-o" aria-hidden="true"></i>
                                {{problem.timeLimit}}ms
                            </info-badge>
                            <info-badge data-toggle="tooltip" data-placement="top" title=""
                                        data-original-title="Memory Limit">
                                <i class="fa fa-cog" aria-hidden="true"></i>
                                {{(problem.memoryLimit / 1024).toFixed(0)}}KB
                            </info-badge>
                            <info-badge v-if="problem.isSpj" style="color: red">
                                Special Judge
                            </info-badge>
                        </div>
                        <div class="animated pre-animated cm-performance-optimistic cm-delay fadeInLeft">
                            <fresh-container>
                                <div style="text-align: center;font-family: Consolas;white-space: pre;">
                                    <h3 style="">{{problem.problemId + ': ' + problem.title}}</h3>
                                </div>
                                <div style="margin-top: 1rem; font-size: 17px;">
                                    <i aria-hidden="true" class="fa fa-bars"></i>
                                    <span> Problem Information</span>
                                </div>
                                <div class="problem">
                                    <div class="card">
                                        <div class="card-header" style="color: black;">
                                            <span>Problem Description</span>
                                        </div>
                                        <div class="card-body">
                                            <span style="white-space: pre;" v-html="problem.problemDescribe"></span>
                                        </div>
                                    </div>
                                    <div class="card">
                                        <div class="card-header" style="color: black;">
                                            <span>Input Description</span>
                                        </div>
                                        <div class="card-body">
                                            <span style="white-space: pre;" v-html="problem.inputDescribe"></span>
                                        </div>
                                    </div>
                                    <div data-v-8b7d93b8="" class="card">
                                        <div class="card-header" style="color: black;">
                                            <span>Output Description</span>
                                        </div>
                                        <div class="card-body">
                                            <span style="white-space: pre;" v-html="problem.outputDescribe"></span>
                                        </div>
                                    </div>
                                    <template v-for="(sample,index) in problem.sampleIO">
                                        <div class="double-card" v-bind:key="key">
                                            <div class="card" style="width: 48%; float: left;">
                                                <div class="card-header" style="color: black;">
                                                <span>
                                                    Sample Input&nbsp;{{index+1}}&nbsp;
                                                    <a v-if="copy.id == index && copy.ing == true && copy.type == 0"
                                                       href="javascript:void(0)">
                                                        <i class="fa fa-check-circle" aria-hidden="true"></i>
                                                    </a>
                                                    <a v-else href="javascript:void(0)"
                                                       @click="handleCopy(sample.sample_I, $event, index, 0)">
                                                    <i class="fa fa-files-o" aria-hidden="true"></i>
                                                </a>
                                                </span>
                                                </div>
                                                <div class="card-body">
                                                <span style="white-space: pre;" v-bind:id="'sampleinput'+index"
                                                      class="sampledata">{{sample.sample_I}}</span>
                                                </div>
                                            </div>
                                            <div class="card" style="width: 48%; float: right;">
                                                <div class="card-header" style="color: black;">
                                                <span>
                                                    Sample Output&nbsp;{{index+1}}&nbsp;
                                                    <a v-if="copy.id == index && copy.ing == true && copy.type == 1"
                                                       href="javascript:void(0)">
                                                        <i class="fa fa-check-circle" aria-hidden="true"></i>
                                                    </a>
                                                    <a v-else href="javascript:void(0)"
                                                       @click="handleCopy(sample.sample_O, $event, index, 1)">
                                                        <i class="fa fa-files-o" aria-hidden="true"></i>
                                                    </a>
                                                </span>
                                                </div>
                                                <div class="card-body">
                                                <span style="white-space: pre;" v-bind:id="'sampleoutput'+index"
                                                      class="sampledata">{{sample.sample_O}}</span>
                                                </div>
                                            </div>
                                            <div style="clear: both;"></div>
                                        </div>
                                    </template>
                                </div>
                            </fresh-container>
                        </div>
                    </div>

                </left-side>
                <middle-slider>
                </middle-slider>
                <right-side
                        style="width: 49.82vw;padding-bottom: 10px;padding-top: 10px;padding-right:5px;background-color: black">
                    <!--<button class="btn btn-secondary" style="margin: 8px;color:white">555</button>-->
                    <editor theme="twilight" v-model="page_setting.content" lang="c_cpp" height="100%"
                            @init="initEditor"
                            :options="editorOptions"></editor>
                </right-side>
            </top-side>
            <bottom-side>
                <div>
                    <button type="button" v-bind:class="{'btn':true, 'btn-secondary': true, 'cm-active': problem_open}"
                            id="problemBtn" @click="togglt_problem_button">
                        <i class="fa fa-book fa-fw" aria-hidden="true"></i>
                    </button>
                    <button type="button" v-bind:class="{'btn':true, 'btn-secondary': true, 'cm-active': edit_open}"
                            id="editorBtn" @click="togglt_edit_button">
                        <i class="fa fa-pencil-square-o fa-fw" aria-hidden="true"></i></button>
                    <button type="button" class="btn btn-secondary" id="historyBtn">
                        <i class="fa fa-history fa-fw" aria-hidden="true"></i>
                        History
                    </button>
                </div>
                <div>
                    <div class="btn-group dropup">
                        <button type="button" class="btn btn-secondary dropdown-toggle" id="cur_lang_selector"
                                data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <template v-if="page_setting.lang == 0">
                                <i class="devicon-c-plain colored"></i> C
                            </template>
                            <template v-else-if="page_setting.lang == 1">
                                <i class="devicon-cplusplus-plain colored"></i> C++
                            </template>
                        </button>
                        <div class="dropdown-menu cm-scrollable-menu">
                            <button class="dropdown-item lang-selector" @click="switch_lang(0)"><i
                                    class="devicon-c-plain colored"></i> C
                            </button>
                            <button class="dropdown-item lang-selector" @click="switch_lang(1)"><i
                                    class="devicon-cplusplus-plain colored"></i> C++
                            </button>
                            <!--<button class="dropdown-item lang-selector" data-coid="3" data-comp="java" data-lang="java"-->
                            <!--data-lcode="java"><i class="devicon-java-plain colored"></i> Java-->
                            <!--</button>-->
                            <!--<button class="dropdown-item lang-selector" data-coid="32" data-comp="python"-->
                            <!--data-lang="python" data-lcode="py2"><i class="devicon-python-plain colored"></i>-->
                            <!--Python 2.7-->
                            <!--</button>-->
                            <!--<button class="dropdown-item lang-selector" data-coid="33" data-comp="python"-->
                            <!--data-lang="python" data-lcode="py3"><i class="devicon-python-plain colored"></i>-->
                            <!--Python 3.7-->
                            <!--</button>-->
                            <!--<button class="dropdown-item lang-selector" data-coid="121" data-comp="php" data-lang="php"-->
                            <!--data-lcode="php7"><i class="devicon-php-plain colored"></i> PHP 7.3-->
                            <!--</button>-->
                            <!--<button class="dropdown-item lang-selector" data-coid="122" data-comp="javascript"-->
                            <!--data-lang="javascript" data-lcode="jsc"><i-->
                            <!--class="devicon-javascript-plain colored"></i> Javascript-->
                            <!--</button>-->
                        </div>
                        <button type="button" class="btn btn-primary" id="submitBtn" @click="submit_code">
                            <i class="fa fa-paper-plane fa-fw" aria-hidden="true"></i>
                            Submit Code
                        </button>
                        <button type="button" class="btn btn-primary"
                                id="resultBtn">
                            <i v-if="page_setting.result == -1" class="fa fa-dot-circle-o fa-fw" aria-hidden="true"
                               style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_WAITING"
                               class="fa fa-spin fa-cog fa-fw " aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_COMPILING"
                               v-bind:class="'fa fa-spin fa-cog fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_RUNNING_JUDGING"
                               v-bind:class="'fa fa-spin fa-cog fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_JUDGING"
                               v-bind:class="'fa fa-spin fa-cog fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_ACCEPTED"
                               v-bind:class="'fa fa-check fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_COMPILATION_ERROR"
                               v-bind:class="'fa fa-exclamation-triangle fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_SYSTEM_ERROR"
                               v-bind:class="'fa fa-times fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_WRONG_ANSWER"
                               v-bind:class="'fa fa-times fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_RUNTIME_ERROR"
                               v-bind:class="'fa fa-times fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_PRESENTATION_ERROR"
                               v-bind:class="'fa fa-times fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_TIME_LIMIT_EXCEEDED"
                               v-bind:class="'fa fa-times fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_MEMORY_LIMIT_EXCEEDED"
                               v-bind:class="'fa fa-times fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                            <i v-else-if="page_setting.result == GLOBAL.OJ_RESULT_OUTPUT_LIMIT_EXCEEDED"
                               v-bind:class="'fa fa-times fa-fw '+GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(page_setting.result)"
                               aria-hidden="true" style="font-size: 20px"></i>
                        </button>

                    </div>
                </div>
            </bottom-side>
        </div>
    </div>
</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    import Loadings from "@/components/loading/loading";
    import fun from '@/assets/js/OJFunction'

    export default {
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: "",
                    pid: 0,
                    is_login: false,
                    content: "",
                    lang: 0,
                    sid: 0,
                    result: -1
                },
                editorOptions: {  // 设置代码编辑器的样式
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true,
                    tabSize: 4,
                    fontSize: 18,
                    showPrintMargin: false   //去除编辑器里的竖线
                },
                problem: {
                    submitStatistics: {
                        allSubmitTimes: 0
                    }
                },
                copy: {
                    id: "",
                    ing: false,
                    type: "",
                },
                problem_open: true,
                edit_open: true
            }
        },
        methods: {
            initEditor: function () {
                require('brace/ext/language_tools')
                require('brace/mode/c_cpp');
                require('brace/theme/twilight');
            },
            handleCopy(text, event, i, j) {
                fun.handleClipboard(text, event)
                this.copy.id = i;
                this.copy.type = j;
                this.copy.ing = true;
                var _this = this;
                setTimeout(function () {
                    _this.copy.ing = false;
                    // alert(1);
                }, 500)
            },
            togglt_problem_button: function () {
                if (!this.problem_open) this.problem_open = true;
                else if (this.edit_open) this.problem_open = false;
            },
            togglt_edit_button: function () {
                if (!this.edit_open) this.edit_open = true;
                else if (this.problem_open) this.edit_open = false;
            },
            switch_lang: function (l) {
                this.page_setting.lang = l;
            },
            submit_code: function () {
                // alert(this.page_setting.refresh_token)
                this.page_setting.result = this.GLOBAL.OJ_RESULT_WAITING;
                this.page_setting.sid = 0;
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/submitcode',
                    {
                        pid: this.page_setting.pid,
                        lang: this.page_setting.lang,
                        code: this.page_setting.content
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(function (res) {
                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.page_setting.sid = res.body.OJ_SOLUTION_ID;

                        this.refresh_result();
                    }
                }, function () {
                    console.log('请求失败处理');
                });
            },
            refresh_result: function () {
                console.log(this.page_setting.result)
                console.log(this.page_setting.sid)
                if (this.page_setting.sid != 0 && (this.page_setting.result == -1
                    || this.page_setting.result == this.GLOBAL.OJ_RESULT_WAITING
                    || this.page_setting.result == this.GLOBAL.OJ_RESULT_COMPILING
                    || this.page_setting.result == this.GLOBAL.OJ_RESULT_RUNNING_JUDGING
                    || this.page_setting.result == this.GLOBAL.OJ_RESULT_JUDGING)) {
                    this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/result/sid',
                        {
                            sid: this.page_setting.sid,
                        }, {
                            emulateJSON: true,
                            headers: {
                                OJ_ACCESS_TOKEN: this.page_setting.access_token,
                                OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                            }
                        }).then(function (res) {
                        console.log(res.body)
                        var error_type = res.body.OJ_ERROR_TYPE;
                        if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                            this.page_setting.result = res.body.OJ_RESULT;

                            var _this = this
                            setTimeout(function () {
                                _this.refresh_result();
                            }, 500)
                        }
                    }, function () {
                        console.log('请求失败处理');
                    });
                }

            }
        },
        components: {
            editor: require("vue2-ace-editor"),
            Loadings,
        },
        name: "submitpage",
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            console.log(this.page_setting.access_token);
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.page_setting.pid = fun.getUrlKey("pid");
            if (this.page_setting.pid == null) window.location.href = this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/problemset.html"
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/submitpage',
                {
                    language: this.page_setting.language,
                    pid: this.page_setting.pid
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                var error_type = res.body.OJ_ERROR_TYPE;

                // console.log(error_type == this.GLOBAL.OJ_ERROR_NO_ERROR);
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {

                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];
                    if (res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_ADMIN) || res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_SUPER_ADMIN) ||
                        res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_PROBLEM_ADMIN)) {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_ADMIN);
                    } else {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_USER);
                    }
                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_PROBLEM) {
                    window.location.href = '/problem.html?pid=' + encodeURIComponent(this.page_setting.pid);
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                }
                this.problem = res.body.OJ_PROBLEM;
                document.title = this.problem.title + " | CTGUACM";

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;
                $(document).ready(function () {
                    $(".double-card").each(function () {
                        var height1 = $(this).children(".card:first").children(".card-body").height();
                        var height2 = $(this).children(".card:last").children(".card-body").height();
                        var max_height;
                        if (height1 > height2) max_height = height1;
                        else max_height = height2;
                        $(this).children(".card:first").children(".card-body").css("height", (max_height + 25) + "px");
                        $(this).children(".card:last").children(".card-body").css("height", (max_height + 25) + "px");
                    });

                    var $moux = "49%";
                    $("middle-slider").mousedown(function () {
                        // $("slide-curtain").css("display", "block");
                        $("slide-curtain").fadeIn(0);
                        // $("curtain-slider").css("left", $("left-side").css("width"));
                        $("slide-curtain").mousemove(function (e) {
                            if (e.pageX <= window.innerWidth * 0.9 && e.pageX >= window.innerWidth * 0.4) {
                                // $("curtain-slider").css("left", e.pageX - 0.0035 * window.innerWidth);
                                $moux = e.pageX - 0.0035 * window.innerWidth;
                            }
                            else if (e.pageX > window.innerWidth * 0.9) {
                                // $("curtain-slider").css("left", window.innerWidth * 0.9 - 0.0035 * window.innerWidth);
                                $moux = window.innerWidth * 0.9 - 0.0035 * window.innerWidth;
                            }
                            else {
                                // $("curtain-slider").css("left", window.innerWidth * 0.25 - 0.0035 * window.innerWidth);
                                $moux = window.innerWidth * 0.4 - 0.0035 * window.innerWidth;
                            }
                            var $leftlen = $moux / window.innerWidth * 100;
                            // $leftlen = "calc(" + $leftlen + 'vw' + " - 48px - 48px)"
                            var $rightlen = (window.innerWidth * (1 - 0.0035) - $moux) / window.innerWidth * 100;

                            $("left-side").css("width", $leftlen + "vw");
                            $("right-side").css("width", $rightlen + "vw");

                        });
                    });

                    $("slide-curtain").mouseup(function () {
                        // alert($moux);
                        // var $len = $(".check-bar").css("left");
                        $("slide-curtain").fadeOut(0);
                        console.log($moux);

                    });
                });
            }, function () {
                console.log('请求失败处理');
            });
        }
    }
</script>

<style scoped lang="scss">
    #submitpage {
        #resultBtn {
            border-left-width: 1.5px;
            border-left-style: solid;
            border-left-color: lightgrey;
        }
        #cur_lang_selector > i {
            padding-right: 0.25rem;
        }
        [class^="devicon-"], [class*=" devicon-"] {
            display: inline-block;
            transform: scale(1.3);
            padding-right: 1rem;
            /*color: #7a8e97;*/
        }
        @media (min-width: 576px) {
            .container-pro {
                max-width: 540px;
            }
        }

        @media (min-width: 768px) {
            .container-pro {
                max-width: 720px;
            }
        }

        @media (min-width: 992px) {
            .container-pro {
                max-width: 960px;
            }

        }

        @media (min-width: 1200px) {
            .container-pro {
                max-width: 1140px;
            }

        }

        .container-pro {
            width: 100%;
            padding-right: 15px;
            padding-left: 15px;
            margin-right: auto;
            margin-left: auto;
        }
        top-side.problem-only > left-side {
            width: 100% !important;
        }

        top-side.editor-only > left-side {
            display: none;
        }

        top-side.problem-only > middle-slider,
        top-side.editor-only > middle-slider {
            display: none;
        }

        top-side.problem-only > right-side {
            display: none;
        }

        top-side.editor-only > right-side {
            width: 100% !important;
        }
        #problemBtn.cm-active, #editorBtn.cm-active {
            box-shadow: inset rgba(0, 0, 0, 0.25) 0px 0px 15px;
        }
        #problemBtn, #editorBtn {
